import React from 'react';
import WorkCard from './WorkCard';

const LatestWorks = ({lang,works}) => {
    return (
        <div className="latest-works__box">
            { lang === 'en' ? <h2 className='latest-works__h'>Latest works</h2>: <h2 className='latest-works__h'>Последние работы</h2> } 
            <div className='latest-works__slider'>
                {
                    works.map((work , i) => {
                        return (
                            <WorkCard 
                                key={i}
                                name={works[i].name}
                                dis={lang === 'en' ? works[i].disEn : works[i].disRu}   
                                clName={works[i].className}
                            />
                        );
                    })
                }
            </div>
            
        </div> 
    );
}

export default LatestWorks;