import React from 'react';
import SkillCard from './SkillCard';
import css from '../../img/logo/css.svg';
import html from '../../img/logo/html.svg';
import javascript from '../../img/logo/javascript.svg';
import react from '../../img/logo/react.svg';

const logos = [css, html, javascript, react];



const Skills = ({skills, lang}) => { 
    return(
        <div className='skillBlock'>
            {
                skills.map((skill , i) => {
                    return (
                        <SkillCard 
                            key={i}
                            name={skill.name}
                            text={lang === 'en' ? skill.textEn : skill.textRu}
                            src={logos[i]}      
                        />
                    );
                })
            }
        </div>
    );
}

export default Skills;